<template>
  <div class="product-box">
    <Row>
      <Col span="6">
      <ul class="menu-ul">
        <li :class="[item.index === isActiveIndex ? 'menu-li-click':'menu-li']" :key="item.index"  v-for="item in productMenuList"  @click='menuClick(item.index)'>
          <!-- <router-link to="{path:'/product/list/'+item.index}"> -->
            <Icon type="md-document" />
            <Icon type="record"></Icon>
            
            {{item.text}}
          <!-- </router-link> -->
        </li>
      </ul>
      </Col>
      <Col span="18">
        <router-view :productList='productList' :productDetail=obj></router-view>
      </Col>
    </Row>
  </div>
</template>

<script>
import {productMenuList,productList1,productList2,productList3,productList4,productList5,productList6} from "./list.config"
// import productList from "./components/list"
// import productDetail from "./components/detail"

export default {
  name: 'Product',
  // components:{
  //   'product-list' : productList,
  //   'product-detail' : productDetail,
  // },
  mounted(){
    let id = this.$route.params.id
    this.menuClick( id * 1)
  },
  watch: {
    '$route' (to, from) {
      let id = to.path.split('/')[3]
      // console.log(to)
      this.updata( id * 1)
      this.isActiveIndex = id*1
    }
  },
  data () {
    return {
      theme:'light',
      productMenuList,
      productList: productList1,
      obj:{},
      isActiveIndex:0,
    }
  },
  methods:{
    getImgSrc(path){
      return require('../Product/img/'+path)
    },
    menuClick(id){
      console.log('menuClick',id)
      this.isActiveIndex = id
      this.$router.push({name: 'productList',params:{id:id}})
      this.updata(id)
    },
    updata(id){
      let list = []
       switch (id){
        case 1:
          list = productList1
          break
        case 2:
          list = productList2
          break
        case 3:
          list = productList3
          break
        case 4:
          list = productList4
          break
        case 5:
          list = productList5
          break
        case 6:
          list = productList6
          break
      }
      this.productList = list
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
.product-box{
  background:#fff;
  height:100%;
  width:1000px;
  margin: 30px auto;

  .li-box{
    /* float: left; */
    /* background:blueviolet; */
    width: 230px;
    height: 210px;
    margin:10px;
    .li-box-img{
      display: block;
      height: 180px;
      width: 230px;
    }
    .li-title-box{
      background:#D5D9E4;
      height:30px;
      font-size: 14px;
      text-align: left;
      color: #fff;
      line-height: 30px;
      padding-left:10px;
    }
  }
  
  .menu-ul{
    width:200px;
    .menu-li{
      height:40px;
      background:#dbe2ef;
      border-bottom: 1px solid #eee;
      line-height: 40px;
      text-align: left;
      padding-left: 10px;
      color: #fff;
      font-size: 14px; 
    }
    .menu-li-click{
       height:40px;
      border-bottom: 1px solid #eee;
      line-height: 40px;
      text-align: left;
      padding-left: 10px;
      color: #fff;
      font-size: 14px; 
      background:#3f72af;
    }
    .menu-li:hover{
      background:#3f72af;
    }

  }
}

</style>



