<template>
  <Row >
    <Col span = "6" class='li-box' :key="item.imgSrc" v-for="item in productList">
      <div @click="goDetail(item)">
        <img class='li-box-img' :src="getImgSrc(item.imgSrc)" alt="">
        <div class='li-title-box'>
          <span>{{item.text}}</span>
        </div>
      </div>
    </Col>
  </Row>
</template>

<script>
// import {productList1,productList2,productList3,productList4,productList5,productList6} from "../list.config"

export default {
name: 'productList',
  mounted(){
    // let id = this.$route.params.id
    // this.menuClick( id * 1)
  },
  props: {
    productList:{
      type: Array
    }
  },
  data () {
    return {

    }
  },
  methods:{
    getImgSrc(path){
      return require('../img/'+path)
    },
    goDetail(item){
      // console.log(JSON.stringify(item))
      this.$router.push({name:'productDetail',query:{id:item.id}})
    }
  }
}

</script>
<style scoped lang='less'>
  .li-box{
    /* float: left; */
    /* background:blueviolet; */
    width: 230px;
    height: 210px;
    margin:10px;
    .li-box-img{
      display: block;
      height: 180px;
      width: 230px;
    }
    .li-title-box{
      background:#bbc5d6;;
      height:30px;
      font-size: 14px;
      text-align: left;
      color: #fff;
      line-height: 30px;
      padding-left:10px;
    }
  }
  </style>


