<template>
  <div >
    <Row>
      <Col span='14'>
        <Carousel autoplay v-model="value2" loop>
          <CarouselItem v-for="item in obj.imgList" :key='item'>
              <div class="demo-carousel">
                <img class='img' :src="getImgSrc(item)" alt="">
              </div>
          </CarouselItem>
        </Carousel>
      </Col>
      <Col span='10'>
        <div class='left-box'>
          <h2 class='left-title'>Specifications:</h2>
          <div v-for="(val,key,i) in obj.techTitle">
            <Tag checkable color="blue">{{key}}：</Tag>  {{val}}<br/>
          </div>
        </div>
      </Col>
    </Row>
    <Row style="margin-top:20px;line-height: 26px;text-align: left;">
      <Tabs type="card">
        <TabPane label="Overview" v-html='obj.overview'>
        </TabPane>
        <TabPane label="Technical">
          <img :src="getImgSrc(obj.technical)" alt="">
        </TabPane>
    </Tabs>
    </Row>
  </div>
</template>

<script>
import {detailList} from "../list.config"

export default {
name: 'productDetail',
  mounted(){
    let id = this.$route.query.id
    this.selectObj(id)
  },
  props: {
    productDetail:{
      type: Object
    }
  },
  data () {
    return {
      value2:0,
      obj: {}
    }
  },
  methods:{
    getImgSrc(path){
      return require('../img/'+path)
    },
    selectObj(id){
      let arr = detailList.filter((item)=> item.id === id)
      this.obj = arr[0].obj
      console.log(this.obj)
    }
  }
}

</script>
<style scoped lang='less'>
.demo-carousel{

  width:100%;
  text-align: center;

  .img{
    width:100%;
    height:auto;
  }
}
.left-title{
  text-align: left;
  margin-bottom: 20px;
}
.left-box{
  font-size: 14px;
  color: #666;
  line-height: 30px;
  text-align: left;
  padding-left:20px;
}
  </style>


