<template>
  <div class="box">
      <Carousel autoplay v-model="value2" loop>
        <CarouselItem>
            <div class="demo-carousel">
              <img src="../../assets/banner1.png" alt="">
            </div>
        </CarouselItem>
        <CarouselItem>
            <div class="demo-carousel">
              <img src="../../assets/banner2.jpg" alt="">
            </div>
        </CarouselItem>
        <CarouselItem>
            <div class="demo-carousel">
              <img src="../../assets/banner3.png" alt="">
            </div>
        </CarouselItem>
    </Carousel>
    <Row class="content">
      <Divider orientation="left">
        <img src="../../assets/In_pro_title.jpg" alt="">
      </Divider>
      <Row >
        <Col span = "6" class='li-box' v-for="item in productImgList">
          <div @click="goDetail(item)">
            <img class='li-box-img' :src="getImgSrc(item.imgSrc)" alt="">
            <div class='li-title-box'>
              <span>{{item.text}}</span>
            </div>
          </div>
        </Col>
      </Row>
    </Row>
  </div>
</template>

<script>
import {productImgList} from "./list.config"
export default {
  name: 'Home',
  data () {
    return {
     value2: 0,
     productImgList
    }
  },
  methods:{
    getImgSrc(path){
      return require('../Product/img/'+path)
    },
    getBannerImg(path){
      return require('../../assets/'+path)
    },
    goDetail(item){
      this.$router.push({name:'productDetail',params:{productDetail:item}})
    }
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.demo-carousel{
  /* width:100%; */
  width:100%;
	height:500px;
  /* background:pink; */
  /* text-align: center; */
  /* line-height: 300px; */
}

  .content{
    width:1000px;
    margin: 0 auto;
    .box{
      text-align: center
    }
  }
.li-box{
  /* float: left; */
  /* background:blueviolet; */
  width: 230px;
  height: 200px;
  margin:10px;
}
.li-box:hover {
    transform: scale(1.1);
}
.li-box-img{
  display: block;
  height: 170px;
  width: 230px;
}
.li-title-box{
  background:#bbc5d6;
  height:30px;
  font-size: 14px;
  text-align: left;
  color: #fff;
  line-height: 30px;
  padding-left:10px;
}
</style>



