<template>
  <div class="hello">
    <img class='about-img' src="../../../static/aboutUs.jpg" alt="">
    <p class='text'>
      Geotech Construction Materials Corp. is established in 2010,  is a leading manufacturer of geosynthetic products. Our main products including geotextile, geomembrane, geogrid, etc, which are mainly used in roads, railways, water conservancy, electricity, soil and water conservation and environmental greening and infrastructure. Relying on the high quality product and professional service&sourcing team, Geotech has developed to be a souring office for many clients.
<br />Our commitment to innovation, our focus on quality and our industry expertise allow us the flexibility to collaborate with our clients to develop a customer-oriented solution. 

    </p>
    <p></p>
  </div>
</template>

<script>
export default {
  name: 'AboutUs',
  data () {
    return {
      msg: 'About Us'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  background:#fff;
  height:100%;
}
.about-img{
  width: 700px;
}
.text{
  width: 700px;
  margin: 20px auto;
  font-size: 16px;
  line-height: 24px;
  text-align: left
}
</style>



