<template>
  <div class="product-box">
    <Row>
      <Col span="6">
      <ul class="menu-ul">
        <li :class="[item.index === isActiveIndex ? 'menu-li-click':'menu-li']" :key="item.index"  v-for="item in projectMenuList"  @click='menuClick(item.index)'>
          <!-- <Icon type="md-document" /> -->
            {{item.text}}
          </li>
      </ul>
      </Col>
      <Col span="18">
        <Row >
        <Col span = "24" :key="item.imgSrc" class='li-box' v-for="item in projectList">
            <div class='li-title-box'>
              <p v-html="item.text"></p>
            </div>
            <img class='li-box-img' :src="getImgSrc(item.imgSrc)" alt="">
        </Col>
      </Row>
      </Col>
    </Row>
  </div>
</template>

<script>
import {projectMenuList,projectList1,projectList2,projectList3,projectList4,projectList5,projectList6} from "./list.config"
export default {
  name: 'Project',
  mounted(){
    let id = this.$route.params.id
    this.menuClick( id * 1)
  },
  watch: {
    '$route' (to, from) {
      let id = to.path.split('/')[2]
      this.menuClick( id * 1)
    }
  },
  data () {
    return {
      theme:'light',
      projectMenuList,
      projectList: projectList1,
      isActiveIndex:0
    }
  },
  methods:{
    getImgSrc(path){
      return require('../Project/img/'+path)
    },
    menuClick(id){
      let list = []
      this.isActiveIndex = id
      switch (id){
        case 1:
          list = projectList1
          break
        case 2:
          list = projectList2
          break
        case 3:
          list = projectList3
          break
        case 4:
          list = projectList4
          break
        case 5:
          list = projectList5
          break
        case 6:
          list = projectList6
          break
      }
      this.projectList = list
    }
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
.product-box{
  background:#fff;
  height:100%;
  width:1000px;
  margin: 30px auto;

  .li-box{
    /* float: left; */
    // /* background:blueviolet;
    // height: 300px;

    margin:10px;
    .li-box-img{
      display: block;
      height: 300px;
    }
    .li-title-box{
      font-size: 14px;
      text-align: left;
      line-height: 26px;
      padding-top: 20px;
      padding-left: 10px
    }
  }
  
  .menu-ul{
    width:200px;
    .menu-li{
      height:40px;
       background:#dbe2ef;
      border-bottom: 1px solid #eee;
      line-height: 40px;
      text-align: left;
      padding-left: 10px;
      color: #fff;
      font-size: 14px; 
    }
    .menu-li-click{
       height:40px;
      border-bottom: 1px solid #eee;
      line-height: 40px;
      text-align: left;
      padding-left: 10px;
      color: #fff;
      font-size: 14px; 
      background:#3f72af;
    }
    .menu-li:hover{
      background:#3f72af;
    }

  }
}

</style>



