import Vue from 'vue'
import Router from 'vue-router'
import AboutUs from '@/components/AboutUs'
import Home from '@/components/Home'
import ContactUs from '@/components/Contact'
import Product from '@/components/Product'
import Project from '@/components/Project'
import productList from '@/components/Product/components/list'
import productDetail from '@/components/Product/components/detail'



export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: AboutUs
    }, {
      path: '/product',
      name: 'product',
      component: Product,
      children: [
        { path: 'list/:id', name: "productList", component: productList },
        { path: 'detail/:id', name: "productDetail", component: productDetail }]
    },
    {
      path: '/project/:id',
      name: 'project',
      component: Project
    },
    {
      path: '/contact',
      name: 'contactUs',
      component: ContactUs
    }
  ]
})

Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}


